@import './scss/media';

html,
body {
  font-family: Bebas Neue, sans-serif;
  box-sizing: border-box;
  padding: 0;
  margin: 0;
  background: whitesmoke;
}

h1 {
  text-align: center;
  margin-top: 25px;
  margin-bottom: 15px;
  font-size: 40px;
  letter-spacing: 5px;
}

.loader {
  position: fixed;
  top: 0;
  bottom: 0;
  left: 0;
  right: 0;
  background: rgba(255, 255, 255, 0.8);

  img {
    position: fixed;
    top: 50%;
    left: 50%;
    transform: translate(-50%, -50%);
  }
}

.img-container {
  margin: 10px 30%;

  img {
    width: 100%;
  }
}